$sub-color: red;
$main-color: #fff;
$text-color: #a1a6a8;
$error-color: #e75555;

@mixin shrinkLabel {
  top: -2px;
  padding: 13px 0 0 20px;

}

.group {
  position: relative;
  margin: 10px 0;
  background-color: white;

  .form-input {
    font-size: 18px;
    text-indent: 16px;
    padding: 10px 10px 10px 9px;
    display: block;
    width: 100%;
    height: 60px;
    border: none;
    border-radius: 4px;
    margin: 25px 0;
    background-color: $main-color;
    color: #a1a6a8;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12);

    &.isError {
      box-shadow: 0 4px 8px 0 rgba(249, 75, 87, 0.12);
      border: solid 1px #f94b57;
      color: #e75555
    }

    &:focus {
      outline: none;
      border-left: 4px solid $sub-color;
      padding-left: 5px;
    }

    &.keyboardfocus {
      outline: none;
      border-left: 4px solid $sub-color;
      padding-left: 5px;
    }

    // &:focus ~ .form-input-label {
    //   @include shrinkLabel();
    // }

  }

  // input[type='password'] {
  //   letter-spacing: 0.3em;
  // }

  .form-input-label {
    font-size: 15px;
    font-weight: bold;
    position: absolute;
    pointer-events: none;
    left: 5px;

    &.shrink {
      @include shrinkLabel();
    }

    &.error-message {
      bottom: 10px;
      padding: 13px 0 0 20px;
      color: $error-color;
      font-size: 12;
      font-weight: 400;
    }
  }

  .form-input-label-show {
    font-size: 11px;
    color: rgba(72, 81, 86, 0.5);
    float: right;
    margin-top: -72px;
    position: relative;
  }
}
