$background-color: #f94b57;
$text-color: #fff;

.custom-button {
  min-width: 165px;
  width: auto;
  height: 43px;
  letter-spacing: 0.5px;
  margin: 20px 0;
  font-size: 13px;
  background-color: $background-color;
  color: black;
  color: $text-color;
  border-radius: 4px;
  // box-shadow: 0 4px 8px 0 rgba(249, 75, 87, 0.27);
  border: solid 1px #f94b57;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.custom-button-inverse {
  background-color: transparent;
  color: #f94b57;
  border: solid 1px f94b57;
}