.sign-in-container {
  font-family: Arial, Helvetica, sans-serif;
  align-self: flex-start;
  height: 100%;
  margin: 40px 70px 0 99px;
  color: #a1a6a8;
}

.form-container {
  margin: 0 0;
  width: 400px;
  margin-top: 20px;
}

.welcome-message {
  text-align: center;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
}

.logo-contanier {
  width: 160px;
  margin-bottom: 46px;
  width: 100%;
  text-align: center;
}

.footer-container {
  margin-top: 50px;
}

.error-message {
  color: #e75555;
  padding-top: 30px;
}

.logo-image {
  max-width: 300px;
}